/*global WP_API_Settings: false*/


(function ($) {
    'use strict';

    function resize() {
        setTimeout(function () {
            // Main container
            var max = $('.mce-tinymce')
                .css('border', 'none')
                .parent().outerHeight();

            // Menubar
            max += -$('.mce-menubar.mce-toolbar').outerHeight();

            // Toolbar
            max -= $('.mce-toolbar-grp').outerHeight();

            // Random fix lawl - why 1px? no one knows
            max -= 1;

            // Set the new height
            $('.mce-edit-area').height(max);
        }, 200);
    }

    $(window).on('resize', function () {
        resize();
    });

})(jQuery);
